<template>
  <div id="result" class="guarantee">
    <div class="inner">
      <div class="titleBox">
        <h2>
          <img src="~@/assets/img/guarantee/img_title01.png" alt="" />
          <img src="~@/assets/img/guarantee/landscape/img_title01.png" alt="" />
          <!-- <span @click="onclickPopup(4)"><em>{{$store.state.timer}}</em></span> -->
          <span><em>{{$store.state.timer}}</em></span>
        </h2>
      </div>
      <div class="contentBox">
        <div class="inner">
          <div class="visualContents">
            <div class="guaranteePopup">
              <p>   <br>   !</p>
              <ul>
                <li
                  v-for="(disease, index) in diseaseData"
                  :key="`disease-${index}`"
                  @click="onclickPopup(index)"
                >
                  <img :src="require(`@/assets/img/guarantee/icon_disease0${index+1}.png`)" alt="" />
                  <span>{{disease.title}}</span>
                  <strong><span>{{calcStatistics[index]}}</span> {{disease.text}}</strong>
                </li>
              </ul>
            </div>

            <p>
              <span>  ,  ?</span>
                  <br>
                  !
            </p>

            <div class="btnBox">
              <button type="button" @click="resetSurvey()"></button>
            </div>
          </div>
        </div>
      </div>
    </div>

    <transition name="layerPopupEnter" mode="out-in">
      <div id="layerPopup" v-show="popupOpen">
        <div class="inner">
          <div class="visual">
            <img
              v-for="(disease, index) in 5"
              :key="`disease-${index}`"
              :src="require(`@/assets/img/guarantee/img_popup${index+1}.png`)"
              :class="{on : currentPopup == index}"
              alt=""
            />
          </div>
          <div class="visual">
            <img
              v-for="(disease, index) in 5"
              :key="`disease-${index}`"
              :src="require(`@/assets/img/guarantee/landscape/img_popup${index+1}.png`)"
              :class="{on : currentPopup == index}"
              alt=""
            />
          </div>
          <button type="button" @click="popupClose()"></button>
        </div>
      </div>
    </transition>

  </div>
</template>
<script>
export default {
  data(){
    return {
      diseaseData: [
        {
          title: '',
          text: ''
        },
        {
          title: '',
          text: ''
        },
        {
          title: ' ',
          text: ''
        },
        {
          title: '',
          text: ''
        }
      ],
      popupOpen: false,
      currentPopup: 0,
    }
  },
  mounted() {
    
  },
  computed: {
    calcStatistics(){
      var result = [];
      var timer = this.$store.state.timer;
      var statisticsData = [0.57, 0.58, 0.47, 0.21];

      statisticsData.forEach(el => {
        var calc = Number((timer * el).toFixed(1));
        result.push(calc);
      });
      return result
    },
  },
  methods: {
    onclickPopup(value){
      this.currentPopup = value;
      this.popupOpen = true;
    },
    popupClose(){
      this.popupOpen = false;
    },
    resetSurvey(){
      this.$router.push('/');
      this.$store.commit('resetSurvey');
    },
  },
}
</script>